import request from "../util/request"

// 
export function requestByGet(api, params, responseType) {
    return request({
        url: api,
        method: "get",
        params: params,
        responseType: responseType
    })
}

// 
export function requestByPost(api, formData, params) {
    return request({
        url: api,
        method: "post",
        data: formData,
        params: params
    })
}

// 
export function requestByPut(api, formData) {
    return request({
        url: api,
        method: "put",
        data: formData
    })
}

// 
export function requestByDelete(api, ids) {
    return request({
        url: api,
        method: "delete",
        data: ids
    })
}
