/** axios
 * 
 */

import Axios from "axios"
import router from "@/router/index"
import { msgBox, customizeMsgBox } from "@/util/msgbox"
import api from "@/api/apis"

// 
const apis = {
    production: "http://zhupu.zhiying2023.cn/estate/v1", //  ()
    development: "http://localhost:9090/estate/v1", //  ()
    accessHomeData: "http://www.xmyxapp.com" // api
}

const axios = Axios.create({
    baseURL: process.env.NODE_ENV === "production" ? apis.production : apis.development,
    timeout: 1000 * 12
})

const releaseUrl = [api.managerLogin]
// 
axios.interceptors.request.use(
    config => {
        if (releaseUrl.indexOf(config.url) > -1) {
            config.headers = {
                "Content-Type": "application/json"
            }
            return config
        } else {
            const jwt = localStorage.getItem("jwt")
            if (jwt !== "" && jwt !== undefined && jwt !== null) {
                config.headers = {
                    "Content-Type": "application/json",
                    jwt: jwt
                }
                return config
            } else {
                msgBox(4030)
                router.push("/auth/login")
            }
        }
    },
    error => {
        // 
        msgBox(1000)
        return Promise.reject(error)
    }
)

// 
axios.interceptors.response.use(
    // 
    response => {
        console.log(response)
        const token = response.data.token
        const code = response.data.code
        if (token !== null && token !== "" && token !== undefined) {
            localStorage.setItem("token", token)
        }
        if (code !== 2000 && code !== "" && code !== undefined) {
            // 
            msgBox(code)
        }
        if (code === 6000) {
            customizeMsgBox(3, response.data.msg)
        }
        return Promise.resolve(response.data)
    }
)

export default axios
