import { Message } from "element-ui"
import router from "@/router/index"

/**
 * 
 * 
 */

// 
export function msgBox(code) {
    if (code === 5000) {
        Message({
            type: "error",
            message: "",
            center: true
        })
    } else if (code === 4030) {
        Message({
            type: "error",
            message: ",",
            center: true
        })
        router.push("/login")
    } else if (code === 4010) {
        Message({
            type: "error",
            message: ",",
            center: true
        })
        router.push("/login")
    } else if (code === 4120) {
        Message({
            type: "error",
            message: "",
            center: true
        })
    } else if (code === 4160) {
        Message({
            type: "error",
            message: "",
            center: true
        })
    } else if (code === 300) {
        Message({
            type: "warning",
            message: "",
            center: true
        })
    }
}

export function customizeMsgBox(no, msg) {
    if (no === 1) {
        Message({
            type: "success",
            message: msg,
            center: true
        })
    } else if (no === 2) {
        Message({
            type: "info",
            message: msg,
            center: true
        })
    } else if (no === 3) {
        Message({
            type: "warning",
            message: msg,
            center: true
        })
    } else {
        Message({
            type: "error",
            message: msg,
            center: true
        })
    }
}
